@import "vars";
@import "mixins";

.partners {
  @include theme;

  padding: 50px 0;

  &__title {
    @include title;
  }

  &__subtitle {
    @include subtitle;
  }
}

.brands {
  max-width: 832px;
  width: 90%;

  margin: 0 auto;

  &__list {
    display: flex;
    justify-content: space-between;

    height: 70px;

    @include _760 {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      grid-row-gap: 30px;

      height: fit-content;
    }

    @include _540 {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__button {
    @include button;

    margin: 50px auto 0;
  }

  &__image {
    height: 100%;
    width: auto;

    transition: transform $a-short;

    &:hover {
      transform: scale(1.1);
    }
  }
}